import React, { useState, useContext, useRef, useEffect } from 'react'
import { UserContext } from '../../provider/UserContext'

import axios from 'axios'

const CommentInput = (props) => {
  const { state } = useContext(UserContext)
  const [username, setUsername] = useState('')
  const [content, setContent] = useState('')
  const textarea = useRef(null)
  const nameArea = useRef(null)

  // ç¬¬ä¸€æ¬¡åŠ è½½ç•Œé¢çš„æ—¶å€™è°ƒç”¨ï¼Œä½¿å…‰æ ‡ç§»åŠ¨åˆ°è¾“å…¥æ¡†
  useEffect(() => {
    // TODO: ä»¥åè¯´ä¸å®šä¼šæœ‰æ›´å¥½çš„è·å¾—å½“å‰ç”¨æˆ·åçš„æ–¹æ³•
    axios.get('/api/users/userInfo').then((res) => {
      if (res.data.code === 0) {
        setUsername(res.data.data.username)
        textarea.current.focus()
      } else {
        nameArea.current.focus()
      }
    })
  }, [])

  const handleSubmit = (e) => {
    e.preventDefault()

    if (props.onSubmit) {
      const { email } = state.userData
      console.table(state.userData)
      console.log(email)
      props.onSubmit({
        username,
        email,
        content,
        createdTime: +new Date(),
      })
    }
    setContent('')
  }

  return (
    <div className='comment-input'>
      <div className='comment-field'>
        <span className='comment-field-name'>ãƒ¦ãƒ¼ã‚¶åï¼š</span>
        <div className='comment-field-input'>
          <input
            value={username}
            ref={nameArea}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
      </div>
      <div className='comment-field'>
        <span className='comment-field-name'>ã‚³ãƒ¡ãƒ³ãƒˆï¼š</span>
        <div className='comment-field-input'>
          <textarea
            ref={textarea}
            value={content}
            onChange={(e) => setContent(e.target.value)}
          />
        </div>
      </div>
      <div className='comment-field-button'>
        <button onClick={(e) => handleSubmit(e)}>æŠ•ç¨¿</button>
      </div>
    </div>
  )
}

export default CommentInput
