import React, { useEffect, useReducer, createContext } from 'react'
// import axios from 'axios'
import { userReducer } from '../reducers/userReducers'
import { checkLogged } from '../actions/userActions'
import { Loading } from '../components/loading/Loading'
let UserContext
const { Provider, Consumer } = (UserContext = createContext())

const initialState = {
  userData: {}, // ç”¨æˆ·dataï¼Œé‡Œé¢åŒ…å«idï¼Œusernameï¼Œemail
  error: null,
  logged: false,
  loading: true,
}
const UserProvider = (props) => {
  const [state, dispatch] = useReducer(userReducer, initialState)
  // TODO: ä¸çŸ¥é“ä¸ºå•¥ç¬¬ä¸€æ¬¡åŠ è½½çš„æ—¶å€™è¿™é‡Œä¼šæ¸²æŸ“3æ¬¡
  useEffect(() => {
    checkLogged(dispatch)
  }, [])

  return (
    <Provider
      value={{
        state,
        dispatch,
      }}
    >
      {state.loading && <Loading />}
      {props.children}
    </Provider>
  )
}

export { UserProvider, Consumer as UserConsumer, UserContext }
